import axios_init from '../../conf/http';
import {HTTP_URL,DEFAULT_SESSION_ID,FID_NUM} from '../../conf/index'

//  (21030)
const TRADR_HEYUE_21030 = 21030;
//  
const TRADE_BUY_12201 = 12201;
//  
const TRADE_SHELL_12201 = 12201;

//  (21030)
export const trade_21030 = (values) => (dispatch) => {
    return axios_init.post(HTTP_URL,{
        hd:{
            "pi":TRADR_HEYUE_21030, 
            "si":DEFAULT_SESSION_ID,
            "sq":1
        },
        bd:{
            ctid:values,
            fid:FID_NUM
        }
    })
}
export const trade_21030_init = (values) => {
    return {
        type:TRADR_HEYUE_21030,
        payload:values
    }
}

//  
export const trade_buy = (values) => (dispatch) => {
    console.log("===")
    console.log(values);
    console.log("===")
    return axios_init.post(HTTP_URL,{
        hd:{
            "pi":TRADE_BUY_12201, 
            "si":DEFAULT_SESSION_ID,
            "sq":1
        },
        bd:{
            fid:FID_NUM,
            ctid:values.ctid,
            "hdorid":-1,
            "ortype":2,//,,int
            "ocflag":1,//,,int
            "bsflag":1,//,,int
            "lttype":0,//,,int
            "orprice":values.orprice, // *1.1 ,,double
            "mtprice":values.mtprice, // *1.1 ,,double
            "qty":values.qty,//,,double
            "rnno":-1,//,,,string
            "rttime":values.rttime,//,,string
            "slprice":values.slprice,  //   100
            "spprice":values.spprice,   // 100
            "point":0,
            "bhqty":0,
            "orderType":1,
            "margin":values.margin // 
        }
    })
}
export const trade_buy_init = (values) => {
    return{
        type:TRADE_BUY_12201,
        payload:values
    }
}

//  
export const trade_shell = (values) => (dispatch) => {
    return axios_init.post(HTTP_URL,{
        hd:{
            "pi":TRADE_SHELL_12201, 
            "si":DEFAULT_SESSION_ID,
            "sq":1
        },
        bd:{
            fid:FID_NUM,//id,,int
            ctid:values.ctid,//id,,int
            "hdorid":values.hdorid,// 
            "ortype":2,//,,int
            "ocflag":2,//,,int
            "bsflag":2,//,,int
            "lttype":0,//,,int
            "orprice":values.orprice,//,  *0.9,double
            "mtprice":values.mtprice, //
            "personid":values.personid,
            "qty":values.qty,//,,double
            "rnno":-1,//,,,string
            "rttime":values.rttime,//,,string
            "slprice":0,
            "spprice":0,
            "point":0,
            "bhqty":0,
            "orderType":1,
            "margin":1000
        }
    })
}
export const trade_shell_init = (values) => {
    return{
        type:TRADE_SHELL_12201,
        payload:values
    }
}



const Trade = {
    trede_21030:{},
    buy_init:{},
    shell_init:{}
}

const reducer = (state = Trade, action) => {
    switch (action.type){
        case TRADR_HEYUE_21030:
            return Object.assign({},state,{
                trede_21030:action.payload
            })
        case TRADE_BUY_12201:
            return Object.assign({},state,{
                buy_init:action.payload
            })
        case TRADE_BUY_12201:
            return Object.assign({},state,{
                shell_init:action.payload
            })
        default:
        return state;
    }
}

//
Date.prototype.Format = function (fmt) { //author: meizz 
    var o = {
        "M+": this.getMonth() + 1, // 
        "d+": this.getDate(), // 
        "h+": this.getHours(), // 
        "m+": this.getMinutes(), // 
        "s+": this.getSeconds(), // 
        "q+": Math.floor((this.getMonth() + 3) / 3), // 
        "S": this.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}
// 
String.prototype.startWith=function(str){     
    var reg=new RegExp("^"+str);     
    return reg.test(this);        
  }  

export default reducer;




